.square {
    background: #fff;
    border: 1px solid #ae54cb;
    font-size: 60px;
    font-weight: bold;
    width: 100px;
    height: 100px;
    padding: 0;
    text-align: center;
    margin-top: -1px;
    margin-left: -1px;
    font-family: 'Play', sans-serif;
    float: left;
    border-radius: 5px;
}
